import "babel-polyfill";
import Spinal from "./spinal.js";


export class GraphData {

  constructor() {
    this.graph = new Spinal();
  }



  /*:******************recupere un promise d'un graphe*************************/
  async loadGraph() {
    return this.graph.load();
  }

  /*********************test si le noeud a des fils**********************/
  haveChildren(node) {
    if (node.getNbChildren() === 0) {
      return false;
    }
    return true;
  }

  /*********************ajouter un noeud**********************/
  async addNode(node) {
    return {
      id: node.getId(),
      name: node.getName(),
      type: node.getType(),
      category: "node",
      contextIds: node.getContextIds(),
      nodes: []
    }
  }




  /*********************ajouter un noeud de realtion**********************/
  async addRelationNode(node) {
    return {
      id: node.getId(),
      name: node.getName(),
      type: node.getType(),
      category: "relation",
      contextId: node.getContextIds(),
      childrens: [],


    }
  }

}



//testttt********************
const t = new GraphData();
const ret = t.loadGraph();

// const res = t.haveChildren(ret);
// console.log(res);

t.loadGraph().then(graph => {
  let tes = t.addRelationNode(graph);
  console.log(tes);

  let test = t.haveChildren(graph);
  console.log(graph);

  console.log("test", test);

});

